@import url(https://fonts.googleapis.com/earlyaccess/jejuhallasan.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: whitesmoke;
  height: 100%
}

.App-header{
  position: fixed;
  top: 0;
  z-index: 100;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  background-color: whitesmoke;
}

.App-title {
  display: flex;
  justify-content: center;
  flex-direction: row-reverse;
  font-family: cursive;
}

.App-title h1{
  margin-bottom: 0px;
}

.App-quotes {
  font-family: 'Jeju Hallasan', cursive;
  font-size: 16px;
  margin-top: 5px;
  padding-left: 50px;
  padding-right: 50px;
}

.App-quotes {
  text-align: center;
}

.App-wrapper {
  margin-top: 180px;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  padding-right:10px;
  padding-left:10px;
}
.item-wrapper {
    width: 230px;
    height: 260px;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #fafafa;
    margin-left: 30px;
    margin-right: 30px;
    margin-bottom: 45px;
    padding-top: 20px;
    border : 1px solid #d6d6d6;
    box-shadow: 0px 0px 10px gray;
}

.item-wrapper:hover {
    box-shadow: 0px 0px 20px red;
    transform: scale(1.05);
}

.item-wrapper-img{
    width: 150px;
    height: 150px;
}

.item-wrapper a{
    text-decoration: none;
    color: #42C0FB;
    margin-top: 5px;
    font-weight: 400;
}

.item-wrapper a:hover {
    font-size: 22px;
    font-weight: 600;
    color: #0276FD;
}

.make_radius {
    border-radius: 100%;
}
